@import "../style/global.scss";

.checkBoxListItem {
    @include box-sizing(content-box);
    @include border-radius(5px);
    @include transition(all 0.1s ease-in-out);
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-align-items: flex-start;
    -webkit-box-align: flex-start;
    -ms-flex-align: flex-start;
    align-items: flex-start;
    cursor: pointer;
    font-family: $default-font;
    position: relative;
    font-size: 17px;
    color: #000;

    &:not(.contentOnly):not(.compact) {
        @include box-shadow(0 0 2px $color-dark-blue);
        background: white;
        margin-bottom: 10px;
    }

    &.contentOnly {
        cursor: auto;
        label {
            cursor: auto;
            padding: 5px 10px;
        }
    }

    &.disabled {
        label {
            cursor: auto;
        }
    }

    &.checked:not(.contentOnly):not(.compact):not(.disabled) {
        @include box-shadow(0 0 0 1px $color-dark-blue, 0 0 4px 0 $color-dark-blue);
    }

    &:not(.checked):not(.contentOnly):not(.compact):not(.disabled):hover {
        @include box-shadow(0 0 6px $color-dark-blue);
    }

    label {
        width: 100%;
        padding: 10px;
        margin: 0;
        cursor: pointer;
    }

    &.compact {
        display: table;
        label {
            padding: 6px 4px;
        }
    }

    input[type="checkbox"] {
        opacity: 0;
        position: absolute;
    }
}
